/* Your app styles here */
.md .bg-color-primary {
    background-color: #222195 !important;
}
.md .bd-color-primary {
    border-color: #222195 !important;
}

.md .checkbox input[type=checkbox]:checked~i, .md label.item-checkbox input[type=checkbox]:checked~* .icon-checkbox, .md label.item-checkbox input[type=checkbox]:checked~.icon-checkbox {
    background-color: #222195 !important;
    border-color: #222195 !important;
}

.md .dialog-button {
    color: #222195 !important;
}

.md .input-focused:after, .md .item-input-focused .item-input-wrap:after {
    background: #222195 !important;
}

.text-color-primary {
    color: #FFFFFF !important;
}

.sheet-modal {
    height: unset !important;
}

.device-desktop .sheet-modal {
    max-width: 500px !important;
    left: 0;
    right: 0;
    margin: auto;
}

.list {
    margin: 0 !important;
}
/* Material Icons Font (for MD theme) */
@font-face {
    font-family: 'Material Icons';
    font-style: normal;
    font-weight: 400;
    src: url(/ma_bjoern/react-nui/static/media/MaterialIcons-Regular.e79bfd88.eot); /* For IE6-8 */
    src: local('Material Icons'),
    local('MaterialIcons-Regular'),
    url(/ma_bjoern/react-nui/static/media/MaterialIcons-Regular.570eb838.woff2) format('woff2'),
    url(/ma_bjoern/react-nui/static/media/MaterialIcons-Regular.012cf6a1.woff) format('woff'),
    url(/ma_bjoern/react-nui/static/media/MaterialIcons-Regular.a37b0c01.ttf) format('truetype');
}

.material-icons {
    font-family: 'Material Icons';
    font-weight: normal;
    font-style: normal;
    font-size: 24px; /* Preferred icon size */
    display: inline-block;
    line-height: 1;
    text-transform: none;
    letter-spacing: normal;
    word-wrap: normal;
    white-space: nowrap;
    direction: ltr;

    /* Support for all WebKit browsers. */
    -webkit-font-smoothing: antialiased;
    /* Support for Safari and Chrome. */
    text-rendering: optimizeLegibility;

    /* Support for Firefox. */
    -moz-osx-font-smoothing: grayscale;

    /* Support for IE. */
    -webkit-font-feature-settings: 'liga';
            font-feature-settings: 'liga';
}

